.page-catalog {
  padding-bottom: 80px;

  &__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @include big-mobile {
      flex-direction: column;
    }
  }

  &__filter {
    flex: 1 0 auto;
    margin-right: 20px;
    width: 180px;

    @include tablet {
      width: 230px;
    }

    @include big-mobile {
      width: 100%;
    }
  }

  &__product {
    @include big-mobile {
      width: 100%;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 20px;
    gap: 10px;

    @include desktop {
      grid-template-columns: repeat(6, 1fr);
    }

    @include big-mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-column: 4 span;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 8px;

    &:nth-child(n+9) {
      display: none;
    }

    &.is-visible {
      display: flex;
    }

    @include desktop {
      grid-column: 3 span;
    }

    @include tablet {
      flex-direction: column;
    }

    @include big-mobile {
      grid-column: 4 span;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #031367;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 14px;
    }
  }

  &__size {
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #031367;

    @include mobile {
      font-size: 14px;
    }
  }

  &__price {
    font-weight: 600;
    font-size: 17px;
    line-height: 130%;
    color: #031367;

    @include mobile {
      font-size: 14px;
    }
  }

  &__img {
    display: block;
    margin-right: 10px;
    width: 100px;
    height: 110px;
  }

  &__show-more {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 10px 18px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #0c5eb8;

    @include mobile {
      width: 100%;
    }
  }

  &__btn-svg {
    width: 24px;
    height: 24px;
    fill: transparent;
    stroke: #0c5eb8;
    stroke-width: 2px;
    transform: translateY(3px);
  }

  @include desktop {
    padding-bottom: 50px;
  }

  @include mobile {
    padding-bottom: 30px;
  }
}

.catalog-filter {
  &__form {
    @include big-mobile {
      display: none;
      margin-top: -10px;
      margin-right: -15px;
      margin-bottom: 20px;
      margin-left: -15px;
      border-radius: 10px;
      padding: 20px;
      background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);
    }
  }

  &__block {
    &:not(:last-child) {
      margin-bottom: 30px;

      @include big-mobile {
        margin-bottom: 6px;
      }
    }

    @include big-mobile {
      border: 1px solid #1078d3;
      border-radius: 10px;
      background: #fff;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    text-align: left;
    color: #0c5eb8;

    @include big-mobile {
      margin: 0;
      padding: 8px 6px;
      font-size: 15px;
    }
  }

  &__item {
    margin-bottom: 10px;
  }

  &__reset {
    margin-top: 20px;
    border-radius: 4px;
    padding: 10px 20px;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

    &:hover {
      background: linear-gradient(63deg, #031367 -37.36%, #031367 95.91%);
    }

    @include big-mobile {
      display: none;
    }
  }
}

.custom-checkbox {
  @include custom-checkbox;

  position: relative;

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    padding-left: 35px;
    font-size: 15px;
    color: #0c5eb8;

    &::before {
      content: "";
      top: -3px;
      border-width: 2px;
      border-color: #0c5eb8;
      border-radius: 4px;
      width: 24px;
      height: 24px;
    }

    &::after {
      content: "";
      left: 3px;
      top: -8px;
      width: 24px;
      height: 24px;
      background-image: url("../img/check.svg");
      background-size: 19px 20px;
    }
  }
}

.btn-aside {
  display: none;
  margin-bottom: 20px;
  border-radius: 10px;
  padding: 12px 30px 12px 10px;
  width: 100%;
  text-align: left;
  background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

  svg {
    width: 24px;
    height: 24px;
    fill: transparent;
    stroke: #fff;
    stroke-width: 2px;
  }

  @include big-mobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.btn-aside__text {
  font-family: var(--second-family, sans-serif);
  font-weight: 400;
  font-size: 14px;
  line-height: 102%;
  letter-spacing: 0.42px;
  color: #fff;

  svg {
    width: 15px;
    height: 15px;
  }
}
