.card-cert {
  margin-right: auto;
  margin-bottom: 80px;
  margin-left: auto;
  width: 100%;
  max-width: 1920px;

  &__slider {
    .hero-slider__btn {
      color: #1078d3;
      background-color: #031367;
      opacity: 0.6;
    }
  }

  &__slide {
    margin-right: auto;
    margin-left: auto;
    padding-right: 20px;
    padding-left: 20px;
    max-width: 400px;

    img {
      display: block;
      border: 2px solid #1078d3;
      border-radius: 10px;
      width: 100%;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
