.footer {
  padding: 30px 0;
  background: #0e1321;

  &__top {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 10px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 200%;
      height: 2px;
      background-color: #fff;
      transform: translateX(-50%);
    }

    @include tablet {
      flex-wrap: nowrap;
      padding-bottom: 40px;
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;
      padding-bottom: 8px;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo-text {
    display: inline-block;
    margin-left: 5px;
    width: 88px;
    font-family: var(--second-family, sans-serif);
    font-size: 12px;
    line-height: 115%;
    letter-spacing: 0.4px;
    text-transform: lowercase;
    color: #fff;

    @include desktop {
      display: none;
    }
  }

  &__list {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    gap: 0 90px;

    @include tablet {
      gap: 0 20px;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    flex: 1 0 auto;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__item--width {
    width: 100%;
    max-width: 538px;
    text-align: right;

    @include tablet {
      flex: auto;
      order: -1;
      text-align: left;
    }

    @include mobile {
      margin-bottom: 10px;
      text-align: center;
    }
  }

  &__item--center {
    text-align: center;
  }

  &__item--artix {
    text-align: end;

    svg {
      width: 35px;
      height: 32px;
      fill: #fff;
      transform: translateY(10px);
    }
  }

  &__link {
    font-weight: 400;
    font-size: 17px;
    line-height: 130%; /* 22.1px */
    letter-spacing: 0.68px;
    color: #fff;

    &[href]:hover {
      opacity: 0.6;
    }

    @include desktop {
      font-size: 13px;
    }
  }

  &__bottom {
    padding-top: 10px;

    .footer__list {
      @include mobile {
        align-items: center;
      }
    }

    @include tablet {
      padding-top: 40px;
    }

    @include mobile {
      padding-top: 8px;
    }
  }

  @include tablet {
    padding: 20px 0;
  }
}
