.video {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 600px;
  max-width: 1180px;

  &__img,
  iframe {
    display: block;
    border: none;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    svg {
      @include mobile {
        width: 100px;
        height: 100px;
      }
    }
  }

  &__play-shape {
    fill: #fff;
  }

  &__play-icon {
    fill: #fff;
  }

  &__play:focus {
    outline: none;
  }

  &:hover .video-play-shape,
  &:focus .video-play-shape {
    fill: #f00;
    fill-opacity: 100%;
  }

  @include desktop {
    padding: 0 15px;
  }

  @include tablet {
    padding: 0;
  }

  @include mobile {
    height: 400px;
  }
}
