.card-related {
  margin-right: auto;
  margin-bottom: 80px;
  margin-left: auto;
  width: 100%;
  max-width: 1920px;

  &__slider {
    padding-right: 10px;
    padding-left: 10px;

    .hero-slider__btn {
      color: #1078d3;
      background-color: #031367;
      opacity: 0.6;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
