.card-term {
  &__wrapper {
    margin-bottom: 80px;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 20px 10px;

    @include tablet {
      margin-bottom: 50px;
    }
  }

  &__list {
    column-count: 2;
    column-gap: 30px;

    @include desktop {
      column-count: 1;
    }
  }

  &__item {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    border-bottom: 1px solid #1078d3;
    padding: 6px 10px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #031367;

    &:first-child {
      flex: 1 0 51%;
      margin-right: 10px;
    }

    &:last-child {
      flex: 1 0 49%;
    }

    @include mobile {
      font-size: 10px;
    }
  }

  &__text--flex {
    &:first-child {
      flex: inherit;
    }

    &:last-child {
      flex: 0 0 auto;
    }
  }
}
