.news-cards {
  margin-bottom: 80px;

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 20px;
    gap: 20px;

    @include desktop {
      grid-template-columns: repeat(4, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__item {
    position: relative;
    grid-column: 4 span;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding-bottom: 20px;

    &:nth-child(n+7) {
      display: none;
    }

    &.is-visible {
      display: block;
    }

    @include desktop {
      grid-column: 2 span;
    }

    @include mobile {
      padding-bottom: 10px;
    }
  }

  &__item-link {
    &:hover .news-cards__title {
      color: #031367;
    }
  }

  &__img {
    display: block;
    margin-bottom: 10px;
    border-radius: 10px;
    width: 100%;
    height: 300px;
    object-position: top;

    @include mobile {
      height: 200px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    padding: 0 10px;
    font-weight: 600;
    font-size: 22px;
    line-height: 130%;
    letter-spacing: 0.66px;
    text-align: center;
    color: #0c5eb8;

    @include desktop {
      font-size: 20px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__text {
    position: relative;
    margin: 0;
    padding: 0 10px;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.6px;
    color: #031367;

    @include desktop {
      font-size: 17px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__svg {
    position: absolute;
    right: 0;
    bottom: 30px;
    width: 80px;
    height: 14px;
    fill: #1078d3;
    stroke: #1078d3;
  }

  &__btn {
    display: inline-block;
    margin: 0 auto;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 10px 18px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #0c5eb8;

    @include mobile {
      width: 100%;
    }
  }

  &__btn-svg {
    width: 24px;
    height: 24px;
    fill: transparent;
    stroke: #0c5eb8;
    stroke-width: 2px;
    transform: translateY(6px);
  }

  &__show-more {
    display: flex;

    @include mobile {
      justify-content: center;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
