.graph-modal__container {
  border-radius: 20px;
  padding: 40px;
  width: 100%;
  max-width: 1200px;
  background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

  .form {
    padding: 50px 0 50px 60px;
    max-width: 60%;

    @include desktop {
      padding: 20px;
      width: 100%;
      max-width: 100%;
    }
  }

  .form__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 500px;
  }

  .form__label {
    width: 50%;

    @include big-mobile {
      width: 100%;
    }
  }

  .form__label-file {
    justify-content: flex-end;
    order: -1;
    margin-bottom: 10px;
    width: 50%;
    height: auto;

    svg {
      position: absolute;
      left: 50%;
      top: 10px;
      transform: translate(-50%, 0);

      @include small-tablet {
        position: relative;
        left: 0;
        top: 0;
        margin-right: 20px;
      }
    }

    @include small-tablet {
      order: 1;
    }

    @include big-mobile {
      width: 100%;
    }
  }

  .form__text {
    margin-bottom: 20px;
  }

  .form__textarea {
    width: 100%;
  }

  .form__inner {
    position: relative;
    width: 40%;

    @include desktop {
      display: none;
    }
  }

  @include desktop {
    width: 95%;
  }

  @include mobile {
    padding: 20px;
  }
}

.graph-modal__content {
  border: 2px solid #fff;
  border-radius: 10px;
  min-height: 500px;
  background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);
}

.graph-modal__content-second {
  display: flex;
  height: 100%;

  .form {
    align-self: center;
  }
}

.graph-modal__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: var(--second-family, sans-serif);
  font-weight: 400;
  font-size: 32px;
  line-height: 130%;
  text-align: center;
  color: #fff;

  @include small-tablet {
    font-size: 22px;
  }

  @include mobile {
    font-size: 18px;
  }
}

.graph-modal__group {
  display: flex;
  justify-content: space-between;
  gap: 20px;

  @include small-tablet {
    flex-direction: column;
  }

  @include big-mobile {
    gap: 0;
  }
}

.graph-modal__span {
  @include small-tablet {
    display: flex;
    justify-content: center;
  }
}

.graph-modal__close {
  right: 50px;
  top: 50px;
  z-index: 10;
  width: 40px;
  height: 40px;
  background-image: url(../img/close.svg);

  @include mobile {
    right: 40px;
    top: 30px;
    width: 30px;
    height: 30px;
  }
}
