.about {
  padding-bottom: 80px;

  .title {
    margin-bottom: 40px;
  }

  &__block {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    gap: 20px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__img {
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    width: 50%;

    @include desktop {
      border-radius: 20px;
    }

    @include big-mobile {
      display: none;
    }
  }

  &__list {
    width: 50%;

    @include big-mobile {
      width: 100%;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 30px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    svg {
      flex: 1 0 auto;
      margin-right: 16px;
      width: 100px;
      height: 100px;
      fill: none;
      stroke: #0c5eb8;
      stroke-width: 8px;

      @include desktop {
        width: 70px;
        height: 70px;
      }

      @include big-mobile {
        width: 55px;
        height: 55px;
      }
    }

    @include desktop {
      padding: 20px 10px;
    }
  }

  &__item-descr {
    @include big-mobile {
      flex: 0 1 100%;
    }
  }

  &__item-title {
    margin: 0;
    margin-bottom: 10px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #0c5eb8;

    @include desktop {
      font-size: 17px;
    }

    @include big-mobile {
      font-size: 12px;
    }
  }

  &__item-text {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #031367;

    @include desktop {
      font-size: 17px;
    }

    @include big-mobile {
      font-size: 12px;
    }
  }

  &__block--reverce {
    flex-direction: row-reverse;

    .about__list {
      @include desktop {
        display: flex;
        width: 100%;
        gap: 20px;
      }

      @include big-mobile {
        flex-direction: column;
      }
    }

    .about__item {
      @include desktop {
        &:not(:last-child) {
          margin-bottom: 0;
        }
      }
    }

    .about__img {
      @include desktop {
        width: 100%;
        height: 300px;
      }
    }

    @include desktop {
      flex-direction: column;
    }
  }

  @include desktop {
    padding-bottom: 50px;
  }

  @include tablet {
    padding-bottom: 30px;
  }
}
