.header {
  padding-top: 15px;
  padding-bottom: 15px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;

    @include big-mobile {
      justify-content: flex-start;
      margin-bottom: 0;
    }
  }

  &__logo {
    display: flex;

    @include big-mobile {
      margin-right: 20px;
      width: 65px;
    }

    @include mobile {
      margin-right: 10px;
    }
  }

  &__logo-text {
    display: inline-block;
    margin-left: 5px;
    width: 88px;
    font-family: var(--second-family, sans-serif);
    font-size: 10px;
    line-height: 115%;
    letter-spacing: 0.4px;
    text-transform: lowercase;
    color: #0c5eb8;

    @include big-mobile {
      display: none;
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    justify-content: space-between;

    svg {
      display: none;
      width: 20px;
      height: 20px;
      fill: #0c5eb8;

      @include big-mobile {
        display: flex;
      }
    }
  }

  &__tel-text {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.6px;
    color: #0c5eb8;
    transition: 0.3s color ease-in;

    &:hover {
      color: #031367;
    }

    @include big-mobile {
      display: none;
    }
  }

  &__bottom {
    position: relative;
    display: flex;
    align-items: center;

    @include big-mobile {
      position: inherit;
    }
  }

  &__catalog {
    margin-right: 40px;
  }

  &__bottom-search {
    border: 2px solid #1078d3;
    border-radius: 10px;
    width: 100%;
  }

  @include big-mobile {
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

.nav {
  &__list {
    display: flex;

    @include tablet {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    line-height: 130%;
    letter-spacing: 0.36px;
    color: #0c5eb8;
    transition: 0.3s color ease-in;

    &:hover {
      color: #031367;
    }
  }
}

.header-catalog {
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 12px 20px;
  background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);
  transition: 0.3s background ease-in;
  cursor: pointer;

  &__text {
    margin: 0;
    margin-left: 5px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 18px;
    line-height: 102%;
    letter-spacing: 0.54px;
    color: #fff;

    @include big-mobile {
      display: none;
    }
  }

  svg {
    margin-right: 5px;
    width: 20px;
    height: 19px;
    stroke: #fff;
  }

  &__list {
    position: absolute;
    left: 0;
    top: 42px;
    z-index: 10;
    display: none;
    border: 3px solid #1078d3;
    border-radius: 10px;
    padding: 30px;
    background: #fff;
    transition: opacity 0.3s ease-in-out;

    &.menu--active {
      display: block;
    }

    @include big-mobile {
      width: 100%;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;

      @include big-mobile {
        margin-bottom: 10px;
      }
    }
  }

  &__link {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 18px;
    line-height: 102%;
    letter-spacing: 0.54px;
    color: #1078d3;

    @include big-mobile {
      font-size: 14px;
    }
  }

  &:hover {
    background: linear-gradient(63deg, #031367 -37.36%, #031367 95.91%);

    @include big-mobile {
      background: transparent;
    }
  }

  @include big-mobile {
    position: absolute;
    right: 20px;
    top: 15px;
    margin-right: 0;
    padding: 0;
    background: transparent;
  }
}

.burger {
  @include burger;
}
