.social {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__item {
    &:not(:last-child) {
      margin-right: 10px;

      @include mobile {
        margin-right: 5px;
      }
    }
  }

  &__link {
    display: inline-block;
    width: 40px;
    height: 40px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__link--wa {
    background-image: url("../img/wa.png");
  }

  &__link--tg {
    background-image: url("../img/tg.png");
  }

  &__link--vb {
    background-image: url("../img/vb.png");
  }

  &__link--tel {
    background-image: url("../img/tel.png");
  }

  @include big-mobile {
    margin-right: 20px;
  }

  @include mobile {
    margin-right: 14px;
  }
}
