.card-size {
  margin-right: auto;
  margin-bottom: 80px;
  margin-left: auto;
  width: 100%;
  max-width: 1920px;

  &__slider {
    padding-right: 10px;
    padding-left: 10px;

    .hero-slider__btn {
      color: #1078d3;
      background-color: #031367;
      opacity: 0.6;
    }

    @include desktop {
      margin-right: 0;
      margin-left: 0;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}

.card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column: 4 span;
  border: 2px solid #1078d3;
  border-radius: 10px;
  padding: 8px;

  &__img {
    display: block;
    margin-right: 10px;
    width: 100px;
    height: 110px;

    @include tablet {
      margin-right: 0;
      margin-bottom: 10px;
    }

    @include mobile {
      margin-right: 10px;
      margin-bottom: 0;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #031367;

    @include tablet {
      font-size: 14px;
    }
  }

  &__size {
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #031367;

    @include mobile {
      font-size: 14px;
    }
  }

  &__price {
    font-weight: 600;
    font-size: 17px;
    line-height: 130%;
    color: #031367;

    @include mobile {
      font-size: 14px;
    }
  }

  @include tablet {
    flex-direction: column;
  }
}
