}

.title {
  margin: 0;
  margin-bottom: 20px;
  font-family: var(--second-family, sans-serif);
  font-weight: 400;
  font-size: 40px;
  line-height: 130%; /* 52px */
  color: var(--txt-h, #0c5eb8);

  &--white {
    color: #fff;
  }

  @include desktop {
    font-size: 24px;
  }

  @include mobile {
    margin-bottom: 10px;
    font-size: 17px;
    text-align: center;
  }
}
