.news {
  margin-bottom: 80px;

  &__text,
  p {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    letter-spacing: 0.72px;
    color: #031367;

    &:not(:last-child) {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 10px;
      }
    }

    @include desktop {
      font-size: 17px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__video {
    position: relative;
    padding-top: 30px;
    text-align: center;

    @include desktop {
      padding-top: 20px;
    }

    @include mobile {
      padding-top: 10px;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
