.contact {
  &__container {
    position: relative;
  }

  &__block {
    position: absolute;
    right: 20px;
    top: 14px;
    z-index: 10;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 50px;
    width: 100%;
    max-width: 580px;
    background: linear-gradient(63deg, rgb(3 19 103 / 95%) -37.36%, rgb(20 148 241 / 95%) 95.91%);

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      display: block;
      border: 2px solid #fff;
      border-radius: 10px;
      width: calc(100% - 40px);
      height: calc(100% - 40px);
      transform: translate(-50%, -50%);

      @include mobile {
        width: calc(100% - 12px);
        height: calc(100% - 12px);
      }
    }

    @include tablet {
      position: relative;
      right: 0;
      top: 0;
      max-width: 100%;
    }

    @include mobile {
      padding: 20px 30px;
    }
  }

  &__block-text {
    z-index: 10;
    margin: 0;
    margin-bottom: 25px;
    padding-left: 50px;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #fff;
    background-position: left center;
    background-size: 20px 20px;
    background-repeat: no-repeat;

    @include tablet {
      padding-left: 40px;
    }

    @include mobile {
      margin-bottom: 16px;
      padding-left: 30px;
      font-size: 15px;
    }
  }

  &__block-text--call {
    background-image: url(../img/call.svg);

    &:hover {
      opacity: 0.6;
    }
  }

  &__block-text--mail {
    background-image: url(../img/mail.svg);

    &:hover {
      opacity: 0.6;
    }
  }

  &__block-text--address {
    background-image: url(../img/address.svg);
  }

  &__social {
    position: absolute;
    right: 50px;
    top: 50px;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;

    @include mobile {
      position: relative;
      right: 0;
      top: 0;
      justify-content: center;
    }
  }

  &__social-item {
    border-radius: 5px;
    width: 40px;
    height: 40px;
    background-color: rgba(#fff, 80%);
  }

  &__social-link {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;

    &:hover {
      opacity: 0.6;
    }
  }

  &__social-link--tg {
    background-image: url("../img/tg.png");
  }

  &__social-link--wa {
    background-image: url("../img/wa.png");
  }

  &__social-link--vb {
    background-image: url("../img/vb.png");
  }

  &__works {
    display: flex;
    flex-direction: column;
    padding-left: 50px;
    background-image: url(../img/clock.svg);
    background-position: left top;
    background-size: 20px 20px;
    background-repeat: no-repeat;

    @include tablet {
      padding-left: 40px;
    }

    @include mobile {
      margin-bottom: 20px;
      padding-left: 30px;
    }
  }

  &__works-text {
    z-index: 1;
    margin: 0;
    margin-bottom: 5px;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: #fff;

    @include mobile {
      margin-bottom: 0;
      font-size: 15px;
    }
  }
}
