/* stylelint-disable color-function-notation */
/* stylelint-disable alpha-value-notation */
/* stylelint-disable number-no-trailing-zeros */
.hero {
  padding-top: 15px;
  background: linear-gradient(117deg, #031367 5.76%, #1494f1 61.99%);

  &__title {
    margin: 0;
    margin-bottom: 14px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 48px;
    line-height: 130%;
    text-align: center;
    color: #fff;

    @include desktop {
      font-size: 28px;
    }

    @include big-mobile {
      font-size: 18px;
    }
  }
}

.hero-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 1920px;

  &__blocks {
    display: flex;
    align-items: flex-end;
    padding-top: 30px;
    padding-bottom: 40px;

    @include big-mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 20px;

    img {
      margin-bottom: 20px;
      width: 450px;

      @include big-mobile {
        margin-bottom: 50px;
        width: 300px;
      }
    }
  }

  &__text {
    width: 100%;
    max-width: 451px;
    font-weight: 500;
    font-size: 22px;
    line-height: 130%;
    color: #fff;

    @include big-mobile {
      margin-bottom: 10px;
      max-width: 100%;
      font-size: 15px;
      text-align: center;
    }
  }

  &__btn-modal {
    margin-bottom: -7px;
    border: 2px solid #1078d3;
    border-radius: 10px;
    padding: 20px 30px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    line-height: 130%;
    color: #0c5eb8;
    background: rgba(255, 255, 255, 0.80);

    &:hover {
      color: #fff;
      background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);
    }

    @include big-mobile {
      padding: 20px 0;
      width: 100%;
    }
  }

  &__img-left {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -10;
    margin-left: 130px;

    img {
      height: 450px;
      object-position: left;
    }
  }

  &__btns {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    width: 100%;
    max-width: 1178px;
    transform: translate(-50%, -50%);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 48px;
    height: 48px;
    color: #fff;
    background-color: #031367;
    opacity: 0.4;

    &::after {
      display: none;
    }

    svg {
      display: block;
      width: 24px;
      height: 24px;
      color: inherit;
      fill: currentcolor;
      -o-object-fit: cover;
      object-fit: cover;
    }

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 1;
    }

    &.swiper-button-disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &__prev {
    position: absolute;
    left: 0;
    z-index: 10;
  }

  &__next {
    position: absolute;
    right: 0;
    z-index: 10;
  }

  &__pag {
    counter-reset: pag;
    left: calc((100% + 768px) / 2) !important;
    bottom: 30px !important;
    display: flex;
    align-items: center;

    .swiper-pagination-bullet {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #fff;
      border-radius: 6px;
      padding: 10px 12px;
      width: 64px;
      height: 64px;
      background-color: transparent;
      opacity: 1;

      &::before {
        counter-increment: pag;
        content: "0"counter(pag);
        font-weight: 700;
        font-size: 24px;
        line-height: 102%;
        letter-spacing: 0.72px;
        color: #fff;
        transition: color 0.3s ease-in-out;

        @include big-mobile {
          font-size: 0;
        }
      }

      &::after {
        display: none;
      }

      @include big-mobile {
        padding: 0;
        width: 6px;
        height: 6px;
      }
    }

    .swiper-pagination-bullet-active {
      border: 3px solid #fff;
      border-radius: 6px;
      font-weight: 700;
      font-size: 32px;
      line-height: 102%;
      letter-spacing: 0.96px;
      color: #fff;
      background-color: transparent;

      @include big-mobile {
        font-size: 0;
      }
    }

    @include desktop {
      display: none;
    }

    @include big-mobile {
      left: 50% !important;
      bottom: 10px !important;
      display: flex;
      justify-content: center;
      transform: translateX(-50%);
    }
  }
}
