.page-form {
  margin-bottom: 80px;
  padding: 80px 0 100px;
  background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__img {
    border: 1px solid #fff;
    border-radius: 10px;
    opacity: 0.9;

    @include desktop {
      width: 368px;
    }

    @include desktop {
      width: 50%;
    }

    @include big-mobile {
      display: none;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include tablet {
    padding: 50px 0 65px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}

.form {
  max-width: 377px;

  &__label {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__input {
    margin-bottom: 10px;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 16px 20px 18px;
    width: 100%;
    height: 56px;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: #fff;
    background-color: transparent;

    &::placeholder {
      color: var(--color-gray);
    }
  }

  &__textarea {
    margin-bottom: 10px;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 16px 20px 18px;
    width: 100%;
    height: 100px;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: #fff;
    background-color: transparent;

    &::placeholder {
      color: var(--color-gray);
    }
  }

  &__label-file {
    margin-bottom: 20px;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 16px 20px 18px;
    width: 100%;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    text-align: center;
    color: #fff;
    background-color: transparent;

    svg {
      position: relative;
      left: -5px;
      top: 5px;
      width: 28px;
      height: 28px;
      fill: #fff;
    }
  }

  &__btn {
    display: block;
    margin: 0 auto;
    margin-bottom: 10px;
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 18px 30px 20px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #0c5eb8;
    background: rgb(255 255 255 / 90%);

    &:hover {
      border: 1px solid #fff;
      color: #fff;
      background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);
    }
  }

  &__text {
    display: inline-block;
    width: 100%;
    font-family: var(--third-family, sans-serif);
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    text-align: center;
    color: #fff;
  }

  @include desktop {
    max-width: 50%;
  }

  @include big-mobile {
    width: 100%;
    max-width: 100%;
  }
}

.accordion__content {
  display: block;

  @include big-mobile {
    display: none;
  }
}

.accordion__icon {
  display: none;
  padding-top: 5px;
  padding-right: 10px;

  svg {
    width: 24px;
    height: 24px;
    fill: transparent;
    stroke: #1078d3;
    stroke-width: 2px;
  }

  @include big-mobile {
    display: inherit;
  }
}

.open .accordion__content {
  display: block;
  padding: 8px 6px;
}

.open .accordion__icon {
  padding: 0 10px;
  transform: rotate(180deg);
}

.accordion__control {
  display: flex;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
}

.open .accordion__control {
  border-bottom: 0.5px solid #1078d3;
}
