.news-photo {
  margin-right: auto;
  margin-bottom: 80px;
  margin-left: auto;
  width: 100%;
  max-width: 1920px;

  &__slide {
    padding-right: 20px;
    padding-left: 20px;

    img {
      display: block;
      border: 2px solid #1078d3;
      border-radius: 20px;
      width: 100%;
      height: 500px;

      @include mobile {
        height: 400px;
      }
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
