/* stylelint-disable alpha-value-notation */
/* stylelint-disable color-function-notation */
.catalog {
  padding-top: 80px;
  padding-bottom: 80px;

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 25px;

    @include desktop {
      grid-template-columns: repeat(6, 1fr);
      gap: 10px;
    }

    @include big-mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 6 span;

    &:last-child {
      grid-column: 12 span;

      @include desktop {
        grid-column: 4 span;
      }
    }

    @include desktop {
      grid-column: 2 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__item-link {
    position: relative;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 40px;
    height: 100%;
    box-shadow: 0 6px 4px 0 rgba(0, 0, 0, 0.06);
    background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      display: block;
      border: 2px solid  #fff;
      border-radius: 10px;
      width: calc(100% - 40px);
      height: calc(100% - 40px);
      transform: translate(-50%, -50%);

      @include desktop {
        width: calc(100% - 20px);
        height: calc(100% - 20px);
      }
    }

    &:hover {
      background: #1078d3;
    }

    @include desktop {
      padding: 20px;
    }
  }

  &__img {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    max-height: 240px;
    object-fit: cover;
  }

  &__item-title {
    margin: 0;
    margin-top: auto;
    padding-right: 90px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #fff;

    &::after {
      content: "";
      position: absolute;
      right: 40px;
      bottom: 50px;
      display: block;
      width: 86px;
      height: 19px;
      fill: #fff;
      background-image: url(../img/catalog-arr.svg);
      background-size: cover;
      background-repeat: no-repeat;

      @include desktop {
        display: none;
      }
    }

    @include desktop {
      padding-right: 0;
      font-size: 14px;
    }

    @include big-mobile {
      font-size: 12px;
    }
  }

  @include desktop {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @include tablet {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
