.card-descr {
  margin-bottom: 80px;

  &__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @include desktop {
      flex-direction: column;
    }
  }

  &__img {
    display: block;
    margin-right: 20px;
    border: 2px solid #1078d3;
    border-radius: 10px;
    width: 480px;
    height: 384px;

    @include desktop {
      margin-right: 0;
      margin-bottom: 20px;
    }

    @include mobile {
      width: 100%;
      height: 200px;
      object-fit: contain;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #0c5eb8;

    @include mobile {
      font-size: 17px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%; /* 26px */
    color: #031367;

    &:last-child {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 20px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__block {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__block-text {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 22px;
    line-height: 130%;
    color: #031367;

    @include mobile {
      font-size: 20px;
    }

    @include mobile {
      font-size: 17px;
    }
  }

  &__block-text--small {
    font-size: 20px;
  }

  &__block-text--weigth {
    font-weight: 700;
  }

  &__block-btn {
    border-radius: 10px;
    padding: 18px 30px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #fff;
    background: linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%);

    &:hover {
      background: linear-gradient(63deg, #031367 -37.36%, #031367 95.91%);
    }

    @include mobile {
      width: 100%;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
